@use '../base/colors' as *;

$primary: #2962ff;
$secondary: #4caf50;
$danger: #d50000;
$warning: #ffc107;

.btn {
    white-space: nowrap;
    background: #f2f2f2;
    border: 1px solid #808080;
    border-radius: 4px;
    color: #404040;
    cursor: pointer;
    font-size: 1rem;
    font-weight: 400;
    padding: 0.375rem 0.75rem;
    transition: background 0.2s;

    &:hover {
        background: darken(#f2f2f2, 10%);
        text-decoration: none;
    }
}

.btn-primary {
    border: 1px solid $primary;
    background: $primary;
    color: #fff;

    &:hover {
        background: darken($primary, 10%);
    }
}

.btn-secondary {
    background: $secondary;
    color: #fff;

    &:hover {
        background: darken($secondary, 10%);
    }
}

.btn-danger {
    border: 1px solid $danger;
    background: $danger;
    color: #fff;

    &:hover {
        background: darken($danger, 10%);
    }
}


