.nav-container {
    white-space: nowrap;
    overflow: hidden;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0;
    list-style-type: none;
    display: flex;
    justify-content: space-between;
}

.nav-links {
    text-decoration: none;
    padding: 0;
    margin: 0;
    list-style-type: none;
    display: flex;
}

.nav-links li {
    padding: 0.75rem 1.5rem 0.75rem 0;
}

.nav-cta {
    padding: 0;
    margin: 0;
    list-style-type: none;
    display: flex;
}

.nav-cta li {
    padding: 0.75rem 0 0.75rem 1.5rem;
}

/* Navbar 2 */

.nav-container-2 {
    max-width: 1200px;
    margin: 0 auto;
}

.topnav {
    overflow: hidden;
    background-color: #f2f2f2;
}

.topnav a {
    float: left;
    display: block;
    /* color: #f2f2f2; */
    text-align: center;
    padding: 0.875rem 1rem;
    text-decoration: none;
}

/* .topnav a:hover {
  background-color: #ddd;
  color: black;
} */

/* .topnav a.active {
  background-color: #4CAF50;
  color: white;
} */

.topnav .icon {
    display: none;
}

@media screen and (max-width: 600px) {
    .topnav a:not(:first-child) {
        display: none;
    }
    .topnav a.icon {
        float: right;
        display: block;
    }
}

@media screen and (max-width: 600px) {
    .topnav.responsive {
        position: relative;
    }
    .topnav.responsive .icon {
        position: absolute;
        right: 0;
        top: 0;
    }
    .topnav.responsive a {
        float: none;
        display: block;
        text-align: left;
    }
}