.food_detail__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;

    &__nutrition_data {
        grid-row: span 2 / auto;

        &__table {
            width: 100%;
            margin-bottom: 1rem;
        }
    }
    &__add_to_diary {
    }
    &__add_to_meal {
    }
    @media screen and (max-width: 800px) {
        grid-template-columns: 1fr;
    }
    @media screen and (max-width: 600px) {
        grid-template-columns: 1fr;
    }
}
