.date {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 1rem;
}

/* Diet Grid */
.diary_grid {
    display: grid;
    grid-template-columns: auto 4.5fr repeat(9, 1fr);
    gap: 1px;
}

/* Title */
.diary_title {
    font-weight: bold;
    display: flex;
    align-items: center;
    padding: .25rem .5rem;
    box-shadow: 0 0 0 1px #f2f2f2;
}

/* Data */
.diary_item {
    display: flex;
    align-items: center;
    padding: .25rem .5rem;
    box-shadow: 0 0 0 1px #f2f2f2;
}

/* Total */
.diary_total_title {
    padding: .25rem .5rem;
    box-shadow: 0 0 0 1px #f2f2f2; 
}

.diary_total {
    font-weight: bold;
    display: flex;
    align-items: center;
    padding: .25rem .5rem;
    box-shadow: 0 0 0 1px #f2f2f2; 
}


.span-col-3{ grid-column: span 3 / auto; }
.span-col-10{ grid-column: span 10 / auto; }

.diary_empty_row { 
  margin: .25rem ;
  grid-column: span 11 / auto; 
}

.end {
    justify-content: flex-end;
    text-align: end;
}

/* Medium */
@media screen and (max-width: 800px) {
    .diary_grid {
      display: grid;
      grid-template-columns: auto 3fr repeat(5, 1fr);
    }
    .diary_item {
      padding: 0.25rem 0.5rem;
    }
    .hidden-md {
      display: none;
    }
    
    .diary_empty_row { 
      grid-column: span 7 / auto; 
    }
    .span-col-10{ grid-column: span 6 / auto; }
  }
  
  /* Small */
  @media screen and (max-width: 600px) {
    .diary_grid {
      display: grid;
      grid-template-columns: auto 3fr 1fr 1fr;
    }
    .diary_item {
      padding: 0.25rem 0.5rem;
    }
    .hidden-sm {
      display: none;
    }
    .diary_empty_row { 
      grid-column: span 4 / auto; 
    }
    .span-col-10{ grid-column: span 3 / auto; }
  }
  