
.food_list__titlebar {
    background: #007FFF;
    color: #fff;
    font-size: 1.5rem;
    font-weight: bold;
    margin: -1rem -1rem 1rem -1rem;
    padding: .5rem 1rem;
    
    >h1 {
        size: 1.5rem;
        margin: 0;
    }
}

.food_list {
    display: grid;
    grid-template-columns: 3fr repeat(9, 1fr);

    > .end {
        display: flex;
        align-items: center;
    }

    &__title {
        border-bottom: 1px solid #f2f2f2;
        font-weight: bold;
        padding: .25rem 0;
    }
    
    &__item {
        border-bottom: 1px solid #f2f2f2;
        padding: .5rem 0;
        line-height: 1.25;

        &__value {
            margin-right: .25rem;
        }
        &__brand > a {
            color: #000;
            
            &:hover {
                text-decoration: none;
            }
        }
    }
}



.food_detail__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;

    &__nutrition_data {
        grid-row: span 2 / auto;

        &__table {
            width: 100%;
            margin-bottom: 1rem;
        }
    }
    &__add_to_diary {
    }
    &__add_to_meal {
    }
    @media screen and (max-width: 800px) {
        grid-template-columns: 1fr;
    }
    @media screen and (max-width: 600px) {
        grid-template-columns: 1fr;
    }
}

.food__btn {
    padding: .5rem 1rem;
    background: #007FFF;
    color: #fff;
    font-size: .9rem;
    font-weight: bold;
    border-radius: 4px;

    &:hover {
        text-decoration: none;
        background: darken(#007FFF, 10%);
        transition: 0.2s ease-in-out;
    }
}