@use './base';
@use './components';
@use './pages';

@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap");

$link: #0066ff;

html {
    font-family: "Inter", sans-serif;
    height: 100%;
}

body {
    display: flex;
    flex-direction: column;
    background: #f2f2f2;
    min-height: 100%;
    line-height: 1.5;
}

.content {
    flex: 1 0 auto;
}

.container {
    background: #fff;
    margin: 0 auto;
    max-width: 1200px;
    padding: 1rem 1rem 1rem 1rem;
}


h1, h2, h3, h4, h5, h6 {
    margin-top: 0;
    margin-bottom: 0.5rem;
}