
.form-2-col-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 1rem;
}

.form-control {
    -webkit-appearance: none;
    width: 100%;
    padding: 6px 12px;
    min-height: 2.5rem;
    border: 1px solid #e6e6e6;
    /* transition: 0.2s ease-in-out;
  -webkit-transition: all .2s ease-in-out;
  -moz-transition: all .2s ease-in-out;
  -ms-transition: all .2s ease-in-out;
  -o-transition: all .2s ease-in-out; */
    border-radius: 4px;
    outline: none;
    margin-bottom: 1rem;
}

.form-control-no-mb {
    -webkit-appearance: none;
    width: 100%;
    padding: 6px 12px;
    min-height: 2.5rem;
    border: 1px solid #e6e6e6;
    /* transition: 0.2s ease-in-out;
  -webkit-transition: all .2s ease-in-out;
  -moz-transition: all .2s ease-in-out;
  -ms-transition: all .2s ease-in-out;
  -o-transition: all .2s ease-in-out; */
    border-radius: 4px;
    outline: none;
}

.form-control:focus, .form-control-no-mb:focus {
    -webkit-appearance: none;
    /* transition: 0.3s ease-in-out;
  -webkit-transition: all .2s ease-in-out;
  -moz-transition: all .2s ease-in-out;
  -ms-transition: all .2s ease-in-out;
  -o-transition: all .2s ease-in-out; */
    border: 1px solid rgba(13, 110, 253, 0.5);
    -webkit-box-shadow: 0px 0px 10px 10px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 0px 10px 10px rgba(0, 0, 0, 0.75);
    box-shadow: 0px 0px 4px 4px rgba(13, 110, 253, 0.25);
    border-radius: 4px;
}

.form-invalid {
    border: 1px solid var(--error);
}